import { useEffect, useState } from "react";

import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Button, SliderField, Banner } from "@buildo/bento-design-system";
import { Divider } from "@nextui-org/react";
import { FaTemperatureHigh } from "react-icons/fa";
import { ImPower } from "react-icons/im";
import { MdExpandLess, MdExpandMore } from "react-icons/md";
import { RxReset } from "react-icons/rx";
import { TbCircuitPushbutton } from "react-icons/tb";

import useDeviceContext from "@/hooks/useDeviceContext";
import { LineChart, Switch } from "@buildo/bento-design-system";
import { produce } from "immer";
import { useTranslation } from "next-export-i18n";
import { PidStatusParameter } from "@/gateway/pid";

export const KeyboardConfigurationComponents = ({ index }: { index: number }) => {
  const { t } = useTranslation()
  const [settingToggle, setSettingToggle] = useState(true);
  const toggleSettingPanel = () => {
    setSettingToggle(!settingToggle);
  };




  const [duration, setDuration] = useState(0);






  return (
    <Card className="w-auto">
      <CardHeader>
        <div className="flex justify-between">
          <CardTitle>{t('Group')} {index + 1}</CardTitle>
        </div>
        <Divider></Divider>

        <CardDescription>
        </CardDescription>
      </CardHeader>
      <CardContent className="flex flex-col gap-4">

          

        <Banner
          kind={duration == 0 ? "positive": "warning"}
          title={t("Current behaviour")}
          description={duration == 0 ? "Dispense water at group head": `Dispense hot water valve for ${duration} second, group head will NOT open`}
        />


        
        <SliderField
          type="single"
          name="value"
          label={t("Dose duration (seconds)")}
          value={duration}
          onChange={(e: number) => {
            setDuration(e)
          }}
          minValue={0}
          maxValue={10}
          step={0.5}
        />

      </CardContent>
      <CardFooter onClick={toggleSettingPanel} className="justify-center">
        {settingToggle ? <MdExpandLess /> : <MdExpandMore />}
      </CardFooter>
    </Card>
  );

};
