"use client";

import { useState, useEffect } from 'react';
import useDeviceContext from "@/hooks/useDeviceContext";
import { useTranslation } from "next-export-i18n";
import { produce } from "immer";
import { PidStatusParameter } from "@/gateway/pid";

import { Banner, Modal, SliderField, Stack } from "@buildo/bento-design-system";

import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";

import {
  Switch,
  Divider,
  LineChart,
  Button
} from "@buildo/bento-design-system";

import {
  FaTemperatureHigh,
} from "react-icons/fa";
import { ImPower } from "react-icons/im";
import { MdExpandLess, MdExpandMore, MdSettings } from "react-icons/md";
import { RxReset } from "react-icons/rx";
import { TbCircuitPushbutton } from "react-icons/tb";
import { cn } from '@/util';




export const FlowmeterConfigurationComponents = ({ index }: { index: number }) => {
  const { connected , calibration, setCalibration} = useDeviceContext();

  
  const [chartData, setChartData] = useState(
    new Array(100).fill({
      temperature: 0,
      power: 0,
    })
  );


  const { t } = useTranslation()
  const { flowmeterDisplay } = useDeviceContext()


  const [isOpen, setIsOpen] = useState(false)
  





  const SettingComponents = (
    <>
    </>
  );

  const [settingToggle, setSettingToggle] = useState(true);
  const toggleSettingPanel = () => {
    setSettingToggle(!settingToggle);
  };

  const [real, setReal] = useState(flowmeterDisplay[index].flowCount);
  const [maxValue, setMaxValue] = useState(1000)

  useEffect(() => {
    setReal(flowmeterDisplay[index].flowCount)

  }, [flowmeterDisplay, index])


  function handleCalibrate() {
    if (real == 0) return
    setMaxValue(Math.max(real * 2, 200))

    setIsOpen(true)
  }
  function updateFlowmeterCalibration(e: number) {

  }

  function handleUpdateCalibration() {
    var e = real
    var newCalibration = (e / flowmeterDisplay[index].flowCount) * calibration.flowmeter[index]


    console.log('calibration/ ', {
      calibration,
      suggestedValue: e,
      previousValue: flowmeterDisplay[index].flowCount,
      currentCalibration: calibration.flowmeter[index],
      newCalibration: newCalibration
    })

    if (newCalibration < 0 || newCalibration > 1000) {
      console.warn("Invalid calibation")
      return
    }


    setCalibration(produce(calibration, (draft) => {
      draft.flowmeter[index] = newCalibration
      return draft
    }))

  }

  return (
    <>
      {isOpen && (
        <Modal
          size="small"
          title={t("Calibrate flowmeter")}
          onClose={() => setIsOpen(false)}
          primaryAction={{
            label: "OK",
            onPress: () => {
              setIsOpen(false);
              handleUpdateCalibration()
              // onChange(powe
            },
          }}
          secondaryAction={{
            label: "Cancel",
            onPress: () => setIsOpen(false),
          }}
        >
          <Stack space={8}>


            <Banner
              kind="informative"
              title={t("Notice")}
              description={t("This calibration process is to set the conversion between flowmeter pulses count with the actual total weight of water extracted, specify how much it is actually weight to update the conversion")}
              action={{
                label: t("Learn more"),
                onPress: () => {
                  window.open("https://www.google.com");
                },
              }}
            />


            <SliderField
              type="single"
              name="value"
              label={t("Real weight (gram)")}
              value={real}
              onChange={(e: number) => {
                updateFlowmeterCalibration(e)
                setReal(e)
              }}
              minValue={0}
              maxValue={maxValue}
              step={1}
            />
          </Stack>
        </Modal>
      )}

      




      <Card className={cn(
        "w-auto border flex-grow bg-white h-full self-center hover:border-purple-700",
        flowmeterDisplay[index].flowRate > 0 ? " text-green-900 border border-green-600" : "border-black border text-black"
      )}>
        <CardHeader>
          <div className="flex justify-between space-x-1 items-center">
            <CardTitle>{t('Group')} {index + 1}</CardTitle>
            {/* <Button
              onPress={handleCalibrate}
              hierarchy="secondary"
              kind="outline"
              label={t('Calibrate')}
              size="small"
              icon={() => <MdSettings />}
            /> */}

          <button
            onClick={handleCalibrate}
              style={{ borderRadius: '8px', border: '1px solid', padding: '8px' }}
              className='text-back bg-white hover:bg-black hover:text-white'
          >
            <MdSettings className=""/>
          </button>
          </div>
          <Divider></Divider>

          <CardDescription>
            <div className="h-16  flex flex-col justify-evenly space-y-4 mt-6">
              {/* <LineChart
                  hideLegend={false}
                  hideXAxis={true}
                  dataKey="name"
                  categories={["temperature", "power", "target"]}
                  disableAnimation={true}
                  hideYAxis={true}
                  hideTooltip={false}
                  data={chartData}
                />{" "} */}
              <div>
                <h1>{t('Flowrate ')}</h1>
                <h1 className="text-xl">{flowmeterDisplay[index].flowRate} gram/s</h1>
              </div>
              <div>
                <h1>{t('Weight')}</h1>
                <h1 className="text-xl">{flowmeterDisplay[index].flowCount} gram</h1>
              </div>
            </div>
          </CardDescription>
        </CardHeader>
        <CardContent>
          {settingToggle ? (
            <>
              {SettingComponents}
            </>
          ) : null}
        </CardContent>

      </Card>

    </>
  );
};
